@import "/static/scss/vars";

.plover-shadow-settings-panel__popover-content {
  width: 330px;
  padding: 16px;

  .plover-shadow-settings-panel__shadow-values {
    .plover-shadow-settings-panel__shadow-value-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 12px;
      padding: 0 8px;
      border: 1px solid $wp-border-color;
      margin-top: -1px;

      &:first-of-type {
        margin-top: 0;
      }

      .plover-shadow-settings-panel__shadow-value {
        gap: 12px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      .plover-shadow-settings-panel__shadow-value-popover {
        width: 100%;
      }

      .plover-shadow-settings-panel__shadow-color-popover {
        line-height: 0;

        .components-button {
          padding: 0;
        }
      }

      .plover-shadow-settings-panel__shadow-value-controls {
        .components-button {
          height: auto;
          padding: 4px;
        }
      }
    }
  }

  .plover-shadow-settings-panel__shadow-add-layer-button {
    width: 100%;
    justify-content: center !important;
  }
}

// shadow-values editor
.plover-shadow-settings-panel__shadow-value-popover-toggle {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 12px;
}

.plover-shadow-settings-panel__shadow-value-popover-content {
  width: 260px;
  padding: 16px;
}

// text-shadow editor
.plover-shadow-settings-panel__text-shadow-editor {
  .plover-shadow-settings-panel__text-shadow-preview {
    padding: 12px 16px;
    background-position: 0 0, 24px 24px;
    border: 1px solid $wp-border-color;

    p {
      font-size: 24px;
      font-weight: 500;
      margin-top: 2px;
      margin-bottom: 0;
      line-height: 1.25;
    }
  }

  .plover-shadow-settings-panel__text-shadow-presets {
    margin: 24px 0;

    h3 {
      text-transform: uppercase;
      font-size: 13px;
      margin-bottom: 2px;
    }

    > .components-button {
      padding: 0;
      border: none;
      margin-bottom: -1px;
      cursor: pointer;
      display: block;
      height: auto;
      text-align: left;
      width: 100%;

      &:not(:last-of-type) {
        border-bottom-color: transparent;
      }

      &:hover {
        p {
          color: initial;
        }
      }
    }

    > .active {
      outline: 2px solid $wp-primary;
      background: rgba(0, 124, 186, 0.05);
    }
  }
}

// drop / box shadow preview canvas
.plover-shadow-settings-panel__box-shadow-preview-canvas,
.plover-shadow-settings-panel__drop-shadow-preview-canvas {
  background-color: #f8fafc;
  padding: 12px;
  border: 1px solid $wp-border-color;

  .components-button {
    padding: 6px;
    height: auto;
  }

  .plover-shadow-settings-panel__shadow-checked {
    fill: $wp-foreground !important;
    position: absolute;
    z-index: 1;
    top: 12px;
  }
}

// drop / box shadow preview
.plover-shadow-settings-panel__box-shadow-preview,
.plover-shadow-settings-panel__drop-shadow-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  position: relative;

  .square-shadow-preview {
    width: 52px;
    height: 52px;
    border: 1px solid $wp-border-color;
    background-color: #ffffff;
  }

  .polygon-shadow-preview {
    fill: #ffffff;
    color: #ffffff;
  }

  span.text-ellipsis {
    display: inline-block;
    width: 52px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}

// drop / box shadow presets
.plover-shadow-settings-panel__box-shadow-presets,
.plover-shadow-settings-panel__drop-shadow-presets {
  margin: 24px 0;

  h3 {
    text-transform: uppercase;
    font-size: 13px;
    margin-bottom: 2px;
  }
}
